var translations = {
  about: 'krlbell',
  over: 'tbb mint',
  almost: 'majdnem',
  lessthan: 'kevesebb mint'
};

function translate(number, addSuffix, key, comparison) {
  var num = number;

  switch (key) {
    case 'xseconds':
      if (comparison === -1 && addSuffix) return num + ' msodperccel ezeltt';
      if (comparison === -1 && !addSuffix) return num + ' msodperce';
      if (comparison === 1) return num + ' msodperc mlva';
      return num + ' msodperc';

    case 'halfaminute':
      if (comparison === -1 && addSuffix) return 'fl perccel ezeltt';
      if (comparison === -1 && !addSuffix) return 'fl perce';
      if (comparison === 1) return 'fl perc mlva';
      return 'fl perc';

    case 'xminutes':
      if (comparison === -1 && addSuffix) return num + ' perccel ezeltt';
      if (comparison === -1 && !addSuffix) return num + ' perce';
      if (comparison === 1) return num + ' perc mlva';
      return num + ' perc';

    case 'xhours':
      if (comparison === -1 && addSuffix) return num + ' rval ezeltt';
      if (comparison === -1 && !addSuffix) return num + ' rja';
      if (comparison === 1) return num + ' ra mlva';
      return num + ' ra';

    case 'xdays':
      if (comparison === -1 && addSuffix) return num + ' nappal ezeltt';
      if (comparison === -1 && !addSuffix) return num + ' napja';
      if (comparison === 1) return num + ' nap mlva';
      return num + ' nap';

    case 'xmonths':
      if (comparison === -1 && addSuffix) return num + ' hnappal ezeltt';
      if (comparison === -1 && !addSuffix) return num + ' hnapja';
      if (comparison === 1) return num + ' hnap mlva';
      return num + ' hnap';

    case 'xyears':
      if (comparison === -1 && addSuffix) return num + ' vvel ezeltt';
      if (comparison === -1 && !addSuffix) return num + ' ve';
      if (comparison === 1) return num + ' v mlva';
      return num + ' v';
  }

  return '';
}

export default function formatDistance(token, count, options) {
  options = options || {};
  var adverb = token.match(/about|over|almost|lessthan/i);
  var unit = token.replace(adverb, '');
  var result;
  result = translate(count, options.addSuffix, unit.toLowerCase(), options.comparison);

  if (adverb) {
    result = translations[adverb[0].toLowerCase()] + ' ' + result;
  }

  return result;
}